.modal {
  contain: strict;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: grid;
  align-items: center;
  overflow-y: scroll;
  transition-property: opacity, visibility, transform;
  transition-duration: .3s;
  transform: scale3d(1.3, 1.3, 1);
  visibility: hidden;
  opacity: 0;
  z-index: 99;
  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &.active {
    transform: scale3d(1, 1, 1);
    visibility: visible;
    opacity: 1;
  }
}

.modal__wrap {
  padding: 70px 30px;
  width: 100%;
  display: flex;
  justify-content: center;

  @media (max-width: $viewport-s) {
    padding: 50px 0;
  }
}

.modal__inner {
  position: relative;
  display: flex;
  flex-grow: 1;
  background-color: white;
}

.modal-overlay {
  position: fixed;
  visibility: hidden;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  transition-property: visibility, opacity;
  transition-duration: .15s;
  background-color: black;
  z-index: 98;

  &--show {
    opacity: .6;
    visibility: visible;
  }
}

.scroll-lock {
  width: 100%;
  position: fixed;
  overflow-y: scroll;
}

.modal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  border: 0;
  background-color: transparent;
  cursor: pointer;
  transition: opacity .3s;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 50%;
    width: 20px;
    height: 4px;
    background-color: black;
    transition: background-color .3s;
  }

  &::before {
    transform: translate(50%, -50%) rotate(-45deg);
  }

  &::after {
    transform: translate(50%, -50%) rotate(45deg);
  }

  &:hover,
  &:focus, {
    &::before,
    &::after {
      background-color: rgba(0, 0, 0, .5);
    }
  }
}
