@mixin anim-shutter() {
  position: relative;
  z-index: 0;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: -2px;
    bottom: -2px;
    left: -2px;
    right: -2px;
    background-color: $color-blue;
    transform: scale3d(0, 1, 1);
    transition-property: background-color, transform;
    transition-duration: $duration-default;
    transition-timing-function: $timing-default;
  }

  &:hover::before,
  &:focus::before {
    transform: scale3d(1, 1, 1);
  }
}
