@import "variables";
@import "mixins";
@import "fonts";
@import "global";
@import "animation";

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

*:focus:not(:focus-visible) {
  outline: none;
}

:root {
  --offset-page: 30px;
  --header-height: 84px;

  @media (max-width: $viewport-m) {
    --header-height: 68px;
  }

  @media (max-width: $viewport-s) {
    --offset-page: 16px;
    --header-height: 56px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  max-width: calc(1140px + var(--offset-page) * 2);
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--offset-page);
}

.content-img {
  height: 100%;
  object-fit: cover;
  object-position: 50% 0;
  //background-color: $bg-img;
}

.content {
  position: relative;
  max-width: 100%;
  transition: max-width $duration-default;
}

.logo {
  width: 164px;
  height: 24px;
  flex-shrink: 0;
  color: white;
  transition: color $duration-default;

  @media (max-width: 1100px) {
    width: 124px;
    height: 18px;
  }

  @media (max-width: $viewport-s) {
    width: 110px;
    height: 16px;
  }

  &:hover,
  &:focus {
    color: $color-blue;
  }
}

.btn {
  padding: 16px 32px;
  flex-shrink: 0;
  font-weight: 600;
  color: white;
  border: 2px solid $color-blue;

  @media (max-width: $viewport-m) {
    padding: 10px 22px;
  }

  @media (max-width: $viewport-s) {
    width: 100%;
    max-width: 350px;
    margin: 0 auto;
  }

  &--map {
    @include anim-shutter();

    @media (max-width: $viewport-m) {
      text-transform: uppercase;
    }
  }

  &--blue {
    background: $color-blue;
    transition: background-color $duration-default, border-color $duration-default;

    &:hover,
    &:focus {
      border-color: $color-blue-hover;
      background: $color-blue-hover;
    }
  }

  &:active {
    background: $color-blue-active;
  }
}

.title {
  font-weight: 600;
  font-size: 40px;
  line-height: 1.3;
  color: $font-black;

  @media (max-width: $viewport-s) {
    font-size: 28px;
  }
}

.title-border {
  position: relative;
  padding-left: 28px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 4px;
    height: 52px;
    background: $color-blue;
  }

  @media (max-width: $viewport-s) {
    padding-left: 0;

    &::before {
      content: none;
    }
  }
}

.block-top-offset {
  padding-top: 160px;
  padding-bottom: 160px;

  @media (max-width: $viewport-m) {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @media (max-width: $viewport-s) {
    padding-top: 72px;
    padding-bottom: 72px;
  }
}

#spinner {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  background-color: rgba(0, 0, 0, .5);

  &.hidden {
    display: none;
  }

  &::after {
    content: "";
    width: 120px;
    height: 120px;
    background: url("../img/spinner.svg") center/cover;
  }
}

.burger-menu__list,
.header__nav-list,
.footer__nav-list,
.dealers__list,
.social-icons__list,
.documentation__list,
.accordion,
.stable__list,
.why__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.gm-style-iw-d {
  h3 {
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 24px;
    line-height: 1;
    color: $color-blue;
  }

  p {
    font-size: 16px;
    line-height: 1.5;
    letter-spacing: .04em;
    color: $color-gray;
  }
}

