.text-page {
  max-width: 810px;
  margin: 0 auto;
  padding: 80px 30px;
}

.text-page__bg {
  width: 100%;
  margin-bottom: 80px;
  padding-top: 60%;
  background: url("../img/bg/text-page-bg.jpg") center/cover no-repeat;
}

.title-small {
  font-weight: 600;
  font-size: 32px;
  line-height: 1;
  color: $font-black;

  @media (max-width: $viewport-s) {
    font-size: 24px;
    line-height: 1.1;
  }
}

.text-page .title {
  margin-bottom: 64px;

  @media (max-width: $viewport-s) {
    margin-bottom: 25px;
  }
}

.text-page__text-block {
  font-size: 18px;

  p + p {
    margin-top: 30px;
  }

  &--one {
    margin: 24px 0 40px;

    @media (max-width: $viewport-s) {
      margin: 16px 0 40px;
    }
  }
}

ol li {
  font-size: 18px;
  line-height: 2;

  &::marker {
    font-weight: 600;
    color: #3C3C3B;
  }
}

.unnumbered-list {
  padding-left: 30px;
  font-size: 18px;
  line-height: 2;

  li {
    padding-left: 5px;

    &::marker {
      content: '\2013';
      position: absolute;
      font-weight: 600;
      color: $color-blue;
    }
  }
}

.text-page .unnumbered-list {
  margin: 40px 0 40px;
}

.text-page .comment {
  margin: 40px 0 80px;
}

.comment {
  position: relative;
  padding: 10px 16px;
  border-left: 4px solid $color-blue;
  color: #3C3C3B;
}

.comment__text {
  margin-bottom: 24px;
  font-size: 20px;

  @media (max-width: $viewport-s) {
    font-size: 18px;
  }
}

.comment__sign {
  display: flex;
  align-items: center;
}

.comment__avatar-wrap {
  width: 56px;
  height: 56px;
  margin-right: 16px;
  flex-shrink: 0;
  border-radius: 50%;
  overflow: hidden;
}

.comment__author {
  line-height: 1;

  p {
    margin-bottom: 8px;
  }

  small {
    font-size: 12px;
    color: $color-gray;
  }
}

.accordion {
  li + li {
    margin-top: 40px;
  }
}

.accordion__item {
  position: relative;
  border-bottom: 2px solid #F0F0F0;

  &.active {
    .accordion__item-title {
      &::before {
        transform: rotate(45deg);
      }

      &::after {
        transform: rotate(-45deg);
      }
    }

    .accordion__item-content {
      max-height: 100vh;
      padding-bottom: 24px;
    }
  }
}

.accordion__item-title {
  position: relative;
  width: 100%;
  padding: 0 30px 24px 0;
  font-size: 24px;
  line-height: 1;
  color: #3C3C3B;
  text-align: left;
  cursor: pointer;

  @media (max-width: $viewport-s) {
    font-size: 20px;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 7px;
    right: 5px;
    width: 14px;
    height: 2px;
    border-radius: 5px;
    background-color: black;
    transition: transform $duration-default;
  }

  &::before {
    transform: rotate(90deg);
  }

  &::after {
    transform: rotate(0deg);
  }
}

.accordion__item-content {
  max-height: 0;
  padding: 0 30px 0 0;
  font-size: 18px;
  overflow: hidden;
  transition-property: max-height, padding-bottom;
  transition-duration: $duration-default;
  transition-timing-function: ease-in-out;
  color: $color-gray;

  @media (max-width: $viewport-s) {
    font-style: italic;
  }
}
