.why {
  h2 {
    margin-bottom: 32px;

    @media (max-width: $viewport-s) {
      margin-bottom: 24px;
    }
  }
}

.why__list {
  max-width: 750px;
  margin-left: 28px;
  font-size: 20px;
  color: $font-light-black;

  @media (max-width: $viewport-s) {
    margin-left: 0;
    font-size: 18px;
  }

  li + li {
    margin-top: 20px;
  }
}
