.custom-select {
  position: relative;
  display: inline-flex;
  min-width: 50px;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;

  &.active {
    .custom-select__title {
      &::before {
        transform: translate(-5px, -50%) rotate(-45deg);
      }

      &::after {
        transform: translate(5px, -50%) rotate(45deg);
      }
    }

    label {
      max-height: 40px;
    }

    .custom-select__content {
      opacity: 1;
      transform: translate3d(0, 30px, 0);
    }
  }
}

.custom-select__title {
  position: relative;
  width: 100%;
  padding-right: 56px;
  font-weight: 600;
  font-size: 48px;
  background-color: white;
  color: $color-blue;
  line-height: 1;
  text-decoration: underline;
  z-index: 2;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 16px;
    width: 15px;
    height: 3px;
    transition: transform .3s ease-out;
    background-color: $color-blue;
    transform: translate(-5px, -50%) rotate(45deg);
  }

  &::after {
    transform: translate(5px, -50%) rotate(-45deg);
  }

  &:hover {
    border-color: $color-blue-hover;

    &::before,
    &::after {
      background-color: $color-blue-hover;
    }
  }
}

.custom-select__content {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 17px 0;
  margin: 0;
  border: none;
  background-color: white;
  box-shadow: 0 4px 10px rgba(101, 101, 101, .25);
  opacity: 0;
  transition: transform $duration-default, opacity .15s;
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    bottom: 94%;
    left: 50%;
    width: 26px;
    height: 25px;
    background-color: white;
    transform: translate(-50%) rotateZ(124deg) skew(-24deg);
    box-shadow: 0 0 6px 0 rgba(101, 101, 101, .25);
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    width: 34px;
    height: 26px;
    background-color: white;
  }

  input {
    &:checked + label {
      color: $color-blue;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 8px;
        width: 2px;
        height: 18px;
        transform: translateY(-50%);
        background-color: $color-blue;
      }
    }

    &:disabled + label {
      opacity: .6;
      pointer-events: none;
    }
  }

  label {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    max-height: 0;
    padding: 12px 16px;
    font-size: 18px;
    line-height: 1;
    letter-spacing: .04em;
    color: $color-gray;
    transition: max-height .3s;
    cursor: pointer;
    overflow: hidden;

    &:hover {
      color: #D8093A;
    }
  }
}
