.stable {
  background: #2C3037;
  color: white;

  h2 {
    margin-bottom: 64px;
    color: white;

    @media (max-width: $viewport-s) {
      margin-bottom: 40px;
    }
  }
}

.stable__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;

  @media (max-width: $viewport-s) {
    grid-template-columns: initial;
    gap: 40px;
  }
}

.stable__item {
  display: flex;
  padding: 40px 0;
  flex-direction: column;
  align-items: center;

  @media (max-width: $viewport-s) {
    flex-direction: row;
    padding: 0;
  }

  p {
    text-align: center;

    @media (max-width: $viewport-m) {
      br {
        display: none;
      }
    }

    @media (max-width: $viewport-s) {
      text-align: left;
    }
  }
}

.stable__img-wrap {
  display: flex;
  width: 72px;
  height: 72px;
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
  border-radius: 50%;
  flex-shrink: 0;
  background: $color-blue;

  @media (max-width: $viewport-s) {
    width: 56px;
    height: 56px;
    margin: 0 16px 0 0;
  }

  svg {
    width: 40px;
    height: 40px;
    flex-shrink: 0;
    fill: none;

    @media (max-width: $viewport-s) {
      width: 31px;
      height: 31px;
    }
  }
}
