.footer {
  background: linear-gradient(93.46deg, #343538 -13.22%, #14161A 159.97%);
  color: white;
}

.footer__wrap {
  padding-top: 80px;
  padding-bottom: 10px;

  @media (max-width: 860px) {
    padding-bottom: 72px;
  }
}

.footer__top {
  display: flex;
  align-items: flex-start;
  margin-bottom: 85px;

  @media (max-width: 860px) {
    flex-direction: column;
    align-items: center;
  }
}

.footer__logo {
  margin-right: 28px;

  @media (max-width: $viewport-m) {
    display: none;
  }
}

.footer__nav {
  margin: 0 auto;

  @media (max-width: $viewport-m) {
    margin-left: 0;
  }

  @media (max-width: 860px) {
    margin: 0;
    order: 2;
  }
}

.footer__nav-list {
  display: grid;
  grid-template-columns: 190px 240px;
  column-gap: 30px;
  row-gap: 25px;

  @media (max-width: 860px) {
    grid-template-columns: 1fr;
    column-gap: initial;
    row-gap: 40px;
    margin-top: 52px;
    justify-items: center;
  }
}

.footer__top-links {
  display: flex;
  flex-direction: column;
  margin: 0 28px;
  font-weight: 600;
  line-height: 1;

  @media (max-width: 860px) {
    margin: 0 0 32px 0;
  }

  @media (max-width: $viewport-s) {
    font-size: 24px;
  }
}

.footer__nav-link {
  @media (max-width: $viewport-s) {
    font-size: 18px;
  }
}

.footer__nav-link,
.footer__phone,
.footer__email {
  transition: color $duration-default;

  &:hover,
  &:focus {
    color: $color-blue;
  }
}

.footer__phone {
  margin-bottom: 8px;
}

.footer__btn-map {
  padding: 12px 16px;
}

.footer__bottom {
  display: flex;
  justify-content: space-between;
  color: #7C7C7C;

  @media (max-width: 860px) {
    flex-direction: column;
    align-items: center;

    p {
      text-align: center;
    }
  }
}

.footer__copyright {
  flex-shrink: 0;
  margin-right: 20px;

  @media (max-width: 860px) {
    margin: 40px 0 0 0;
    order: 2;
  }
}

.footer {
  @media (min-width: 861px) {
    .social-icons {
      width: initial;
      max-width: 225px;
      margin-left: 20px;
      flex-shrink: 0;
    }

    .social-icons__list {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      gap: initial;
    }

    .social-icons__item {
      width: 24px;
      height: 24px;
      margin-bottom: 16px;

      &:not(:last-child) {
        margin-right: 16px;
      }
    }
  }

  @media (max-width: 860px) {
    .social-icons {
      order: -1;
      margin: 0 0 24px 0;
    }

    .social-icons__list {
      justify-content: center;
    }
  }
}
