.social-icons {
  --icon-size: 32px;
  width: 100%;
}

.social-icons__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, var(--icon-size));
  align-items: center;
  gap: 24px;

  @media (max-width: $viewport-m) {
    --icon-size: 36px;
  }

  @media (max-width: 370px) {
    --icon-size: 28px;
  }

  a {
    fill: none;
    color: $color-menu-item;
    transition: color $duration-default;

    &:hover,
    &:focus {
      color: $color-blue;
    }
  }
}
