.documentation {
  h2 {
    margin-bottom: 80px;

    @media (max-width: $viewport-s) {
      margin-bottom: 40px;
    }
  }
}

.documentation__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  row-gap: 40px;

  @media (max-width: $viewport-s) {
    column-gap: 30px;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  }
}

.documentation__item {
  display: grid;
  grid-template-columns: min-content;
  justify-items: center;

  svg {
    width: 95px;
    height: 95px;
    flex-shrink: 0;
    fill: none;

    @media (max-width: $viewport-m) {
      width: 48px;
      height: 48px;
    }
  }

  &--rst {
    svg {
      width: 87px;
      height: 68px;

      @media (max-width: $viewport-m) {
        width: 44px;
        height: 35px;
      }
    }
  }

  &--expert {
    svg {
      width: 73px;
      height: 59px;

      @media (max-width: $viewport-m) {
        width: 37px;
        height: 30px;
      }
    }
  }
}

.documentation__wrap-img {
  display: flex;
  width: 165px;
  height: 165px;
  justify-content: center;
  align-items: center;
  margin-bottom: 28px;
  border-radius: 50%;
  border: 2px solid #1B1E23;

  @media (max-width: $viewport-m) {
    width: 84px;
    height: 84px;
  }

  svg {
    flex-shrink: 0;
  }
}

.documentation__title {
  text-align: center;
  margin-bottom: 24px;
}

.documentation__link {
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: $viewport-m) {
    font-size: 12px;
  }

  svg {
    width: 18px;
    height: 18px;
    margin-right: 12px;
    flex-shrink: 0;

    @media (max-width: $viewport-m) {
      width: 14px;
      height: 14px;
      margin-right: 8px;
    }
  }
}
