.dealers__title {
  margin: 0 56px 40px 0;
  font-weight: 600;
  font-size: 48px;
  line-height: 1;
  color: $font-black;
}

.dealers__container {
  display: flex;
}

.dealers__map-wrap {
  position: relative;
  width: 55%;
  background-color: $bg-img;
  z-index: 0;

  &::before {
    content: "ЗАГРУЖАЕТСЯ КАРТА ДИЛЕРОВ...";
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    font-size: 30px;
    text-align: center;
    z-index: -1;
  }
}

.dealers__map {
  width: 100%;
  height: 756px;
}

.dealers__item {
  padding: 32px 30px;
  border-bottom: 1px solid #F0F2F4;

  h3 {
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 24px;
    line-height: 1;
    color: #000;
  }

  p {
    letter-spacing: .04em;
    color: $color-gray;
  }
}
