.application {
  background: #F4F6FA;
}

.application__wrap {
  display: flex;
  max-width: 720px;
  flex-direction: column;
  align-items: center;

  @media (max-width: $viewport-s) {
    max-width: initial;
    align-items: flex-start;
  }
}

.application__title {
  margin-bottom: 16px;
  text-align: center;

  @media (max-width: $viewport-s) {
    text-align: left;
  }
}

.application__messages {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform .6s;
}

.sending-status {
  opacity: 0;
  transform: scale3d(1.5, 1.5, 1);
}

.application__text {
  margin-bottom: 40px;
  max-width: 354px;
  font-size: 20px;
  color: $color-gray;
  text-align: center;

  @media (max-width: $viewport-s) {
    max-width: initial;
    font-size: 18px;
    text-align: left;
  }
}

.application__form {
  display: flex;
  width: 100%;
  margin-bottom: 16px;

  @media (max-width: $viewport-s) {
    flex-direction: column;
  }
}

.application__label {
  position: relative;
  flex-grow: 1;

  @media (max-width: $viewport-s) {
    height: 52px;
    margin-bottom: 16px;
  }

  input {
    width: 100%;
    height: 100%;
    padding: 0 16px;
    border-color: transparent;
    background: white;
    transition: border-color $duration-default;

    &::placeholder {
      color: #7C8087;
    }
  }

  [data-error] {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    font-size: 12px;
    line-height: 1.5;
    color: $color-error; // FF3156
    pointer-events: none;

    &::before {
      content: attr(data-error);
      position: absolute;
      bottom: 100%;
      left: 0;
      opacity: 0;
      transition: opacity $duration-default;
    }
  }
}

.validate {
  input:invalid {
    border-bottom: 1px solid $color-error;

    & ~ [data-error]::before {
      opacity: 1;
    }
  }
}

.application__agreement {
  font-size: 14px;
  line-height: 1;
  color: #B6B6B6;

  a {
    color: $color-blue;
    text-decoration: underline;
  }
}

.application__submit {
  @media (max-width: $viewport-s) {
    max-width: 100%;
    min-height: 52px;
  }
}

.form-invalid {
  animation: shake .3s;
}

@keyframes shake {
  0%,
  100% {
    transform: translate3d(0, 0, 0);
  }

  20%,
  60% {
    transform: translate3d(-10px, 0, 0);
  }

  40%,
  80% {
    transform: translate3d(10px, 0, 0);
  }
}
