.models {
  background: #F4F6FA;

  h2 {
    margin-bottom: 48px;

    @media (max-width: $viewport-s) {
      margin-bottom: 40px;
    }
  }
}

.models__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;

  @media (max-width: 1131px) {
    grid-template-columns: repeat(auto-fill, minmax(Min(331px, 100%), 1fr));
  }

  @media (max-width: $viewport-s) {
    grid-template-columns: initial;
  }
}

.models__item {
  display: flex;
  flex-direction: column;
  background: white;
  padding: 24px;
  color: $font-light-black;

  @media (max-width: $viewport-s) {
    max-width: 500px;
    width: 100%;
    justify-self: center;
  }

  h3 {
    margin-bottom: 12px;
    font-weight: 600;
    font-size: Min(32px, 2.7vw);
    line-height: 1;

    @media (max-width: 1131px) {
      font-size: 30px;
    }

    @media (max-width: $viewport-s) {
      font-size: 32px;
    }

    @media (max-width: 375px) {
      font-size: 25px;
    }
  }
}

.models__wrap-img {
  padding: 0 6px;
  margin-bottom: 12px;
}

.models__price {
  margin-bottom: 32px;
  font-weight: 600;
  font-size: 24px;
  line-height: 1;
  color: $color-blue;
}

.models__options {
  margin-bottom: 40px;
  font-size: 14px;

  & > div {
    display: flex;
    justify-content: space-between;
  }

  & > div + div {
    margin-top: 18px;
  }

  dd {
    flex-shrink: 0;
    margin-left: 15px;
    color: $color-gray;
  }
}

.models__btn-wrap {
  margin-top: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

.models__btn {
  padding: 16px 25px;
  text-transform: uppercase;

  @media (max-width: $viewport-m) {
    padding: 10px 22px;
  }

  @media (max-width: 370px) {
    padding: 10px 10px 8px;
  }

  &--more {
    color: $color-blue;
    transition: color $duration-default;

    @include anim-shutter();

    &:hover,
    &:focus {
      color: white;
    }
  }
}
